<template>
  <div>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="时间：">
            <el-date-picker
              v-model="searchList.startDate"
              type="month"
              value-format="yyyy-MM"
              placeholder="选择月">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="交易分类：">
            <el-select v-model="searchList.assetType" clearable>
              <el-option label="店铺余额" value="0"></el-option>
              <el-option label="储值资金" value="1"></el-option>
              <!-- <el-option label="赠送金账户" value="2"></el-option>
              <el-option label="标记资金" value="3"></el-option> -->
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="mt10" shadow="hover">
      <p class="warningFont">对账单统计日期以账单明细中入账时间为准，如需下单时间维度的汇总，可在账单明细下载相应报表进行汇总。</p>
      <p class="warningFont">日汇总账单在次日6时生成，月汇总账单在次月首日6时生成。如遇特殊情况，则可能产生延迟生成情况，请谅解。</p>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="filter-container mt10" shadow="never">
      <el-table ref="multipleTable" :data="list" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <!-- <el-table-column
          type="selection"
          width="55">
        </el-table-column> -->
        <el-table-column prop="balanceDay" label="入账日期" width="240"></el-table-column>
        <el-table-column label="账户">
          <template slot-scope="scope">
            <div :data-id="scope.row.id">店铺余额</div>
          </template>
        </el-table-column>
        <el-table-column prop="incomeAmount" label="收入（￥）"></el-table-column>
        <el-table-column prop="incomeCount" label="收入笔数"></el-table-column>
        <el-table-column prop="expenditureAmount" label="支出（￥）"></el-table-column>
        <el-table-column prop="expenditureCount" label="支出笔数"></el-table-column>
        <el-table-column label="操作" width="240">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="$router.push({path:'/assetsManage/capitalManage/monthDetail',query:{id:scope.row.id}})">费用汇总账单查看</el-button>
            <el-button type="text" size="small" @click="goToList(scope.row)">查看明细</el-button>
            <el-button type="text" size="small" @click="exportDetail(scope.row.balanceDay)">下载明细</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
      <!-- <div class="flex-row-between" style="padding-top:20px;"> -->
        <!-- <el-button type="primary">合并下载账单明细</el-button> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetMonthPage , getMonthDay} from '@/api/assetsManage/capitalManage'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了月')
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      list:[],
      listTotal:0,
      multipleSelection:[],       // 选中的数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    goToList(e){
      getMonthDay(e.balanceDay).then(res1=>{
        console.log('获取日期',res1)
        if(res1.code ==200){
          this.$router.push({
            path:'/assetsManage/capitalManage/detailList',
            query:{
              balanceDay:e.balanceDay,
              startDate:res1.data.startDate,
              endDate:res1.data.endDate,
              in:e.incomeAmount,
              out:e.expenditureAmount,
              dateType:'month'
            }
          })
        }
      })
      
    },
    exportDetail(t){
      let str = `shopId=${this.shop.shopId}&dataStr=${t}`
      window.open(
        `/sys/shopAsset/exportShopAsset?${str}`
      )
    },
    getData(){
      getShopAssetMonthPage(this.searchList).then(res=>{
        console.log('月账单列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total;
        }
      })
    },
    
    reset(){
      this.searchList.startDate = ''
      this.searchList.assetType = ''
      this.getData()
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>