import request from '@/utils/request'
// 查询日账单列表
export function getShopAssetDayPage(data) {
  return request({
    url: `/sys/shopAssetDay/getShopAssetDayPage`,
    method: 'post',
    data
  })
}
// 日账单汇总
export function getShopAssetDayTotal(id) {
  return request({
    url: `/sys/shopAssetDay/getShopAssetDayTotal?id=${id}`,
    method: 'get',
  })
}
// 查询月账单列表
export function getShopAssetMonthPage(data) {
  return request({
    url: `/sys/shopAssetMonth/getShopAssetMonthPage`,
    method: 'post',
    data
  })
}
// 月账单汇总
export function getShopAssetMonthTotal(id) {
  return request({
    url: `/sys/shopAssetMonth/getShopAssetMonthTotal?id=${id}`,
    method: 'get',
  })
}
// 交易账单查询
export function getShopTradBill(data) {
  return request({
    url: `/sys/shopAsset/getShopTradBill`,
    method: 'post',
    data
  })
}
// 日期计算
export function getMonthDay(data) {
  return request({
    url: `/sys/shopAssetMonth/getMonthDay?monthDate=${data}`,
    method: 'get',
  })
}